  align-items: center;
  justify-content: flex-end;
  max-width: 50%;
  margin-inline-start: auto;
  padding-inline-start: 4px;
}

.accordion-content {
  overflow: auto;
}

.accordion-content[hidden] {
  display: none;
}

.accordion-item:last-child > .accordion-content {
  border-bottom: none;
}

