/* Bug 1360238 - getSelection displays an extra "\n" on multiple sibling block elements.
   We rely on this behavior to add an extra "\n" between matched selectors (Bug 1222737).
   Therefore we use <div> elements around matched selectors and need this class
   to keep them inline. We do that to avoid doing any formatting logic in JS.
   Once Bug 1360238 will be fixed, we'll probably have to change the behavior
   and remove this class. */
.fix-get-selection {
  display: inline;
}

.visually-hidden {
  opacity: 0;
}

/* From skin */
.computed-expander {
  visibility: hidden;
}

.computed-expandable {
  visibility: visible;
}

.match {
  visibility: hidden;
}

.matchedselectors > * {
  clear: both;
  margin: 0;
  margin-inline-end: 2px;
  padding: 2px;
  overflow-x: hidden;
  border-style: dotted;
  border-color: rgba(128, 128, 128, 0.4);
  border-width: 1px 1px 0 1px;
}

.matchedselectors > :last-of-type {
  border-bottom-width: 1px;
}

.matched {
  text-decoration: line-through;
}

.parentmatch {
  opacity: 0.5;
}

#computed-no-results {
  height: 100%;
}

.onlyuserstyles {
  cursor: pointer;
}

.legendKey {
  margin: 0 5px;
}

.computed-link {
  padding: 0 3px;
  /* cursor: pointer; */
  float: inline-end;
}

/* Take away these two :visited rules to get a core dumper     */
/* See https://bugzilla.mozilla.org/show_bug.cgi?id=575675#c30 */

.computed-link,
.computed-link:visited {
  text-decoration: none;
}

/* .computed-link:hover {
  text-decoration: underline;
} */

.computed-colorswatch {
  border-radius: 50%;
  width: 0.9em;
  height: 0.9em;
  vertical-align: middle;
  margin-inline-end: 5px;
  display: inline-block;
  position: relative;
}

.computed-colorswatch::before {
  content: "";
  background-color: #eee;
  background-image: linear-gradient(
      45deg,
      #ccc 25%,
      transparent 25%,
      transparent 75%,
      #ccc 75%,
      #ccc
    ),
    linear-gradient(45deg, #ccc 25%, transparent 25%, transparent 75%, #ccc 75%, #ccc);
  background-size: 12px 12px;
  background-position: 0 0, 6px 6px;
  position: absolute;
  border-radius: 50%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.computed-overridden .rule-text,
.computed-overridden .computed-other-property-value {
  text-decoration: line-through solid var(--theme-text-color-alt);
}

